  font-size: 20px;
  justify-content: center;
}
nav {
  background-color: #f5f7f5;
  padding: 10px 50px;
  display: flex;
  justify-content: space-between;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 10px;
}

li {
  display: inline;
  margin-right: 20px;
}

li:last-child {
  margin-right: 0;
}

a {
  color:rgb(26, 58, 240);
  text-decoration: none;
}

a:hover {
  color: #cebdbd;
}

